<template>
  <div>
    <select class="c-select" v-model="province">
      <option v-for="p in areas.provinces" track-by="$index" v-bind:value="p[1]">
        {{p[0]}}
      </option>
    </select>
    <select class="c-select" v-model="city">
      <option v-for="c in areas.cities" track-by="$index" v-bind:value="c[1]">
        {{c[0]}}
      </option>
    </select>
    <select class="c-select" v-model="district">
      <option v-for="d in areas.districts" track-by="$index" v-bind:value="d[1]">
        {{d[0]}}
      </option>
    </select>
  </div>
</template>
<script>
  export default {
    props: ['province', 'city', 'district'],
    data () {
      return {
        areas: {
          provinces: {},
          cities: {},
          districts: {}
        }
      }
    },
    ready () {
      this.$http.get('http://jiancan.me/api/m1/areas/provinces.json').then(function (response) {
        this.$set('areas.provinces', response.data.areas)
      }, function (response) {
        this.showAlert(response.data.error)
      })
    },
    watch: {
      province (val, oldVal) {
        this.$http.get('http://jiancan.me/api/m1/areas/cities.json', { province_id: this.province }).then(function (response) {
          this.areas.cities = response.data.areas
        }, function (response) {
          this.showAlert(response.data.error)
        })
      },
      city (val, oldVal) {
        this.$http.get('http://jiancan.me/api/m1/areas/districts.json', { city_id: this.city }).then(function (response) {
          this.areas.districts = response.data.areas
        }, function (response) {
          this.showAlert(response.data.error)
        })
      }
    }
  }
</script>



