<template>
<nav-bar>
  <span slot="title" class="title">店铺管理</span>
</nav-bar>
<table class="table">
  <tr>
    <td style="width:200px;padding-left:50px;">
      <img @click="inputFileClick" alt="店铺头像" class="img-circle" width="100" height="100" v-bind:src="shop.avatar" />
      <input @change="uploadAvatar" v-el:avatar class="avatar" type="file" accept="image/*" style="display:none;" />
    </td>
    <td>
      <div class="content-form">
        <form @submit="update" accept-charset="UTF-8">
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">店铺名称</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="名称" type="text" v-model="shop.name" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputPassword3" class="col-sm-2 form-control-label">店铺电话</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="电话" type="text" v-model="shop.mobile" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputPassword3" class="col-sm-2 form-control-label">店铺地区</label>
            <div class="col-sm-10">
              <div class="row">
                <div class="city-group" style="padding-left:15px;">
                  <vue-china-city :province.sync="shop.province" :city.sync="shop.city" :district.sync="shop.district"></vue-china-city>
                </div>
              </div>
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">店铺地址</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="地址" type="text" v-model="shop.street" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">提供餐次</label>
            <div class="col-sm-10" style="line-height:38px;">
              <label class="c-input c-checkbox">
                <input type="checkbox" value="breakfast" v-model="shop.meals" />
                <span class="c-indicator"></span>
                早餐
              </label>

              <label class="c-input c-checkbox">
                <input type="checkbox" value="lunch" v-model="shop.meals" />
                <span class="c-indicator"></span>
                中餐
              </label>

              <label class="c-input c-checkbox">
                <input type="checkbox" value="afternoon_tea" v-model="shop.meals" />
                <span class="c-indicator"></span>
                下午茶
              </label>

              <label class="c-input c-checkbox">
                <input type="checkbox" value="supper" v-model="shop.meals" />
                <span class="c-indicator"></span>
                晚餐
              </label>

              <label class="c-input c-checkbox">
                <input type="checkbox" value="night_snack" v-model="shop.meals" />
                <span class="c-indicator"></span>
                宵夜
              </label>

            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label">满额免运费</label>
            <div class="col-sm-10">
              <input class="form-control" placeholder="满额免运费" type="text" v-model="shop.full_free_courier" />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
            <div class="col-sm-10">
              <input type="submit" name="commit" value="保 存" class="btn btn-primary" />
            </div>
          </div>
        </form>
      </td>
    </tr>
  </table>
</div>

</template>
<script>
  import { showAlert, showLoading, hideLoading, showHandleTip } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged,
        access_token: state => state.merchant.merchant.access_token
      },
      actions: {
        showAlert,
        showLoading,
        hideLoading,
        showHandleTip
      }
    },
    data () {
      return {
        shop: {
          name: '',
          province: '',
          city: '',
          district: '',
          street: '',
          avatar: '',
          mobile: '',
          meals: [],
          full_free_courier: 0
        }
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        this.$http.get('http://jiancan.me/api/m1/shops/current.json', { access_token: this.access_token }).then(function (response) {
          this.$set('shop', response.data)
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    methods: {
      inputFileClick (e) {
        return this.$els.avatar.click()
      },
      uploadAvatar () {
        this.showLoading()
        if (this.$els.avatar.value === '') return
        var formData = new FormData()
        formData.append('access_token', this.access_token)
        formData.append('shop[avatar]', this.$els.avatar.files[0])

        this.$http.put('http://jiancan.me/api/m1/shops/one.json', formData).then(function (response) {
          this.$set('shop', response.data.shop)
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      },
      update () {
        this.showLoading()

        var formData = new FormData()
        formData.append('access_token', this.access_token)

        formData.append('shop[name]', this.shop.name)
        formData.append('shop[mobile]', this.shop.mobile)
        formData.append('shop[province]', this.shop.province)
        formData.append('shop[city]', this.shop.city)
        formData.append('shop[district]', this.shop.district)
        formData.append('shop[street]', this.shop.street)
        formData.append('shop[full_free_courier]', this.shop.full_free_courier)
        for (var i = 0; i < this.shop.meals.length; i++) {
          formData.append('shop[meals][]', this.shop.meals[i])
        }

        this.$http.put('http://jiancan.me/api/m1/shops/one.json', formData).then(function (response) {
          this.$set('shop', response.data.shop)
          this.showHandleTip()
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue'),
      'vue-china-city': require('../components/vue-china-city.vue')
    }
  }
</script>



